<template>
	<view style="display: flex;flex-direction: column;width: 100%;">
		<view style="width: 100%;z-index: 1;">
			<image src="../../static/login/qin.png" style="width: 100%;" mode="widthFix"></image>
			
			<view style="position: fixed;width: 100%;top: 44px;text-align: center;">
				<text style="color: #fff;font-size: 16px;">登录</text>
			</view>
		</view>
		
		<view style="padding: 20px;margin-top: -100rpx;z-index: 2;">
			<view class="box_login">
				<input style="height: 40rpx;" disabled="true"/>
				
				<view class="tran_button">
					<text style="color: #fff;" @click="login_pwd()">密码登录</text>
				</view>
				<view class="tran_button">
					<text style="color: #fff;" @click="login_sms()">电话号码登录</text>
				</view>
				
				<view style="text-align: center;">
					<text style="color: #fff;" >或者</text>
				</view>
				
				<view class="white_button">
					<text style="color: #37c8be;" @click="login_yijian()">本机号码一键登录</text>
				</view>
				<view class="white_button">
					<text style="color: #37c8be;" @click="login_qq()">QQ登录</text>
				</view>
				
				<input style="height: 40rpx;" disabled="true"/>
			</view>
		</view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				
			}
		},
		mounted() {
			this.yijian();
		},
		methods: {
			login_pwd(){
				uni.navigateTo({
					url:'/pages/login/pwd'
				})
			},
			login_sms(){
				uni.navigateTo({
					url:'/pages/login/sms'
				})
			},
			yijian(){
				uni.login({
					provider:'univerify',
					univerifyStyle:{
						icon:{
							path:"static/logo.png"
						}
					},
					success: (res) => {
						uniCloud.callFunction({
							name:'yijian',
							data:res.authResult,
							success: (re) => {
								console.log(re);
								if(re.result.code==0){
									uni.showToast({
										icon:'success',
										title:'登录成功'
									});
									//保存token,user
									uni.setStorage({
										key:'token',
										data:re.result.token
									});
									uni.setStorage({
										key:'user',
										data:re.result.user
									});
									uni.setStorageSync('gender',re.result.gender);
									
									uni.switchTab({
										url:'../navigation/ShouYe/ShouYe'
									});
									uni.closeAuthView();
								}else{
									uni.closeAuthView();
									uni.showToast({
										title:re.result.msg,
										icon:'none',
										duration:5000
									});
								}
								
							}
						})
					}
				})
			},
			login_qq(){
				uni.showToast({
					title:'该功能暂未开启',
					icon:'none'
				});
			}
		}
	}
</script>

<style>
	.tran_button{
		width: 80%;
		height: 40px;
		border: thin solid #fff;
		border-radius: 20px;
		text-align: center;
		display: flex;
		flex-direction: column;
		justify-content: center;
		flex-shrink: 0;
	}
	.white_button{
		width: 80%;
		height: 40px;
		border: thin solid #fff;
		border-radius: 20px;
		text-align: center;
		display: flex;
		flex-direction: column;
		justify-content: center;
		background-color: #fff;
		flex-shrink: 0;
	}
	.box_login{
		background: linear-gradient(to top right,#99c5ff,#59f7ad);
		border-radius: 10px;
		display: flex;
		flex-direction: column;
		justify-content: space-around;
		align-items: center;
		height: 700rpx;		
	}
</style>
